import React from 'react';

import {
    BrowserRouter as Router,
    Routes,
    Route
} from "react-router-dom";

// react-router-dom이 버전 6으로 업그레이드 되면서 Switch -> Routes로 바뀌고 컴포넌트 호출도 element로 변경됨
/*
[as-is]
<Switch>
    <Route path="/">
        <Home />
    </Route>
</Switch>
[to-be]
<Routes>
    <Route path="/" element={<Home />} />
</Routes>
*/

import Home from "./routes/Home";
import Detail from "./routes/Detail";

function App() {
    return (
        <Router>
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/movie/:id" element={<Detail />} />
            </Routes>
        </Router>
    );
}

export default App;
